ul {
  list-style-type: none;
}
body {
  font-family: Verdana, sans-serif;
}

/* Month header */
.month {
  padding: 70px 25px;
  width: 100%;
  background: #1abc9c;
  text-align: center;
}

/* Month list */
.month ul {
  margin: 0;
  padding: 0;
}

.month ul li {
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

/* Previous button inside month header */
.month .prev {
  float: left;
  padding-top: 10px;
}

/* Next button */
.month .next {
  float: right;
  padding-top: 10px;
}

/* Weekdays (Mon-Sun) */
.weekdays {
  margin: 0;
  padding: 10px 0;
  background-color: #ddd;
}

.weekdays li {
  display: inline-block;
  width: 13.6%;
  color: #666;
  text-align: center;
}

/* Days (1-31) */
.days {
  padding: 10px 0;
  background: #eee;
  margin: 0;
}

.days li {
  list-style-type: none;
  display: inline-block;
  width: 13.6%;
  text-align: center;
  margin-bottom: 10px;
  font-size: 12px;
  color: #777;
  padding-top: 20px;
}
/* Highlight the "current" day */
  .date:hover {
  padding: 5px;
  width: 10%;
  background: #1abc9c;
  color: white !important;
  border-radius: 5px;
} 



a {
  color: inherit;
  /* text-decoration: inherit; */
}

.highlight {
  padding: 5px;
  width: 10%;
  background: #1d8eeb;
  color: white !important;
  border-radius: 5px;
}
 .wrong:hover {
  padding: 5px;
  width: 10%;
  background: #ff3c00;
  color: white !important;
  border-radius: 5px;
} 